  const [shopItems, setShopItems] = useState(itemList);
  const [cartItems, setCartItems] = useState([]);
  const toggleCart = () => {
    cart === '-400px' ? setCart('0px') : setCart('-400px');
  };

  const showShoppingCart = () => {
    setCart('0px');
  };

  const itemInCart = (itemID) => {
    for (let i = 0; i < cartItems.length; i++) {
      if (cartItems[i].id === itemID) {
        return true;
      }
    }
  };

  const addToCart = (itemID) => {
    if (!itemInCart(itemID)) {
      for (let i = 0; i < shopItems.length; i++) {
        if (shopItems[i].id === itemID) {
          setCartItems([...cartItems, { ...shopItems[i], qty: 1 }]);
          showShoppingCart();
          return;
        }
      }
    }
  };

  const increaseQty = (itemID) => {
    setCartItems(
      cartItems.map((item) =>
        item.id === itemID ? { ...item, qty: item.qty + 1 } : item
      )
    );
  };

  const decreaseQty = (itemID) => {
    setCartItems(
      cartItems.map((item) =>
        item.id === itemID
          ? {
              ...item,
              qty: item.qty <= 1 ? 1 : item.qty - 1,
            }
          : item
      )
    );
  };

  return (
    <Wrapper>
      <LandingPage cartToggle={toggleCart} cartItems={cartItems} />
      <Items shopItems={shopItems} addToCart={addToCart} />
      <Cart
        cartToggle={toggleCart}
        cartItems={cartItems}
        cartPosition={cart}
        decreaseQty={decreaseQty}
        increaseQty={increaseQty}
      />
      <Footer />
      {cart === '0px' && <div className="backdrop"> hello</div>}
    </Wrapper>
  );
