import React from 'react';
import Styled from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faClose } from '@fortawesome/free-solid-svg-icons';
import CartItem from './CartItem';
const Cart = ({
  cartPosition,
  cartToggle,
  cartItems,
  decreaseQty,
  increaseQty,
}) => {
  return (
    <Wrapper cartPosition={cartPosition}>
      <button className="btn-close" onClick={cartToggle}>
        <FontAwesomeIcon icon={faClose} />
      </button>
      <h3>YOUR CART</h3>
      <CartItems>
        {cartItems.map((item, i) => (
          <CartItem
            key={i}
            item={item}
            decreaseQty={decreaseQty}
            increaseQty={increaseQty}
          />
        ))}
      </CartItems>
      <p>
        {`Your Total:Ghc `}
        {cartItems.reduce(
          (sum, item) => (sum + item.price * item.qty).toFixed(2),
          0
        )}
      </p>
      <CartBtn>
        <button>Checkout</button>
        <button>Clear</button>
      </CartBtn>
    </Wrapper>
  );
};
const Wrapper = Styled.div`
position:fixed;
background:#D3E871;
display:flex;
flex-direction:column;
border-left:2px solid ; 
top:0;
right:${(props) => props.cartPosition};  
height:100vh;
transition:.5s ease-in-out;
padding:20px;
z-index:999;
justify-content:space-between;
min-width:300px;

button{
  cursor:pointer;
}
.btn-close{
  width:fit-content;
  padding: 2.5px 10px;
  font-size:20px;

}
  h3{
    text-align:center;  
    margin:20px 0px
  }
 
}

`;
const CartItems = Styled.div`
display:flex;
flex-direction:column;
gap:20px;
margin:5rem 0px;
`;

const CartBtn = Styled.div`
display:flex;
flex-direction:column;
gap:20px;
margin-top:20px;
  button{
    font-size:1.3rem;
    padding:15px;
  }
  & :first-child{
background:green;
  }
  & :last-child{

background:rgb(190, 10, 10);
  }
  & :first-child:hover{
background:rgb(24, 149, 7);
color:white;
  }
  & :last-child:hover{
    background:rgb(206, 10, 10);
color:white;


  }
`;
export default Cart;
