import React from 'react';
import Styled from 'styled-components';
import Item from './Item';
import SearchBox from './SearchBox';
// import useRequestResource from './hooks/useRequestResource';
const Items = ({ shopItems, addToCart }) => {
  return (
    <Wrapper name="shopItems" id="shopItems">
      <ItemsHeader>
        <h3>Shop Items</h3>
        <SearchBox />
      </ItemsHeader>
      <div className="items">
        {shopItems.map((item, i) => (
          <Item key={i} item={item} addToCart={addToCart} />
        ))}
      </div>
    </Wrapper>
  );
};
const Wrapper = Styled.div`
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    gap:30px;
    
    
    
    .items{
      display:grid;
      grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
      gap:50px;
      padding:10px;
      width:80vw;

    }
`;
const ItemsHeader = Styled(Wrapper)`
gap:50px;
h3{
font-size:2rem;
}
`;
export default Items;
